import { createRouter, createWebHistory } from "vue-router";
import Main from "@/features/Main.vue";
import ErrorPage from "@/features/ErrorPage.vue";

const routes = [
  {
    path: "/",
    component: Main,
    meta: { title: "–ì–ª–∞–≤–Ω–∞—è" },
  },
  {
    path: "/hackathons",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/blog",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/portfolio",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "WIP" },
  },
  {
    path: "/cv",
    beforeEnter() {
      window.location.href =
        "https://drive.google.com/file/d/1HKAtFmZcGReYVL7Q8sNidvGesTF1IZNS/view?usp=sharing";
    },
  },
  {
    path: "/:pathMatch(.*)*",
    component: ErrorPage,
    meta: { title: "~errorLabel", errorLabel: "404" },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
